.slider-container {
  margin-top: 16px; }
  .slider-container_title {
    display: flex;
    align-items: flex-end;
    position: relative;
    margin-bottom: 16px; }
    .slider-container_title:after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      bottom: 4px;
      height: 1px;
      background-color: #eaeaea; }
    .slider-container_title h2 {
      font-size: 18px;
      margin-bottom: 0;
      color: #666666;
      background-color: #fff;
      position: relative;
      z-index: 2;
      padding-right: 5px; }
      .slider-container_title h2:before {
        content: '';
        position: relative;
        width: 15px;
        height: 10px;
        bottom: 0px;
        margin-right: 4px;
        background-color: #f01b48;
        display: inline-block; }
  .slider-container :global .slick-slide {
    padding: 0 3px; }

.slick-slider-main {
  padding-top: 20px; }
  .slick-slider-main :global span.lnr-chevron-left, .slick-slider-main :global span.lnr-chevron-right {
    right: 30px;
    position: absolute;
    top: -5px;
    cursor: pointer; }
  .slick-slider-main :global span.lnr-chevron-left {
    right: 25px; }
  .slick-slider-main :global span.lnr-chevron-right {
    right: 3px; }
