.slider_item {
  border: 1px solid #6666;
  border-radius: 6px;
  position: relative;
  padding: 10px; }
  .slider_item .user-options {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    height: 25px; }
    .slider_item .user-options > span {
      cursor: pointer;
      width: 18px;
      height: 18px;
      display: block; }
      .slider_item .user-options > span svg {
        width: 100%;
        height: 100%; }
  .slider_item-top {
    position: relative;
    border-bottom: 1px solid #6666;
    padding: 0 !important;
    margin-top: 5px; }
    .slider_item-top > img {
      width: 100%;
      height: 150px;
      object-fit: cover;
      margin-bottom: 10px; }
      @media screen and (max-width: 576px) {
        .slider_item-top > img {
          height: auto; } }
    .slider_item-top .slider_item-sale_badge {
      position: absolute;
      right: 5px;
      top: 10px;
      background-color: #f01b48;
      color: #fff;
      padding: 2px 8px;
      border-radius: 4px;
      font-size: 12px; }
      .slider_item-top .slider_item-sale_badge:after {
        content: "";
