        height: 0;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-bottom: 5px solid #f01b48;
        position: absolute;
        right: -6px;
        top: 0;
        bottom: 0;
        margin: auto;
        transform: rotate(90deg); }
    .slider_item-top .slider_item-top_actions-links {
      position: absolute;
      bottom: 40px;
      left: 0;
      right: 0;
      display: flex;
      justify-content: center;
      transition: .4s;
      transform: scale(0, 0);
      opacity: 0; }
      .slider_item-top .slider_item-top_actions-links > a {
        text-decoration: none; }
      .slider_item-top .slider_item-top_actions-links > span, .slider_item-top .slider_item-top_actions-links > a > span {
        display: inline-flex;
        width: 25px;
        height: 25px;
        background: #191919;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        color: #fff;
        margin: 0 4px;
        font-size: 12px;
        cursor: pointer; }
        .slider_item-top .slider_item-top_actions-links > span:hover, .slider_item-top .slider_item-top_actions-links > a > span:hover {
          background-color: #f01b48; }
    .slider_item-top:hover .slider_item-top_actions-links {
      transform: scale(1, 1);
      opacity: 1; }
    .slider_item-top > h4 {
      text-align: center;
      font-size: 16px;
      font-weight: 600;
      height: 23px;
      overflow: hidden; }
  .slider_item-bottom_price {
    margin: 10px 0;
    text-align: center; }
    .slider_item-bottom_price_real {
      color: #f01b48;
      font-weight: 600;
      font-size: 20px; }
    .slider_item-bottom_price_del {
      font-size: 13px;
      text-decoration-line: line-through;
      color: #6c757d;
      margin-left: 6px; }
  .slider_item-bottom_stars {
    text-align: center; }
    .slider_item-bottom_stars > span {
      color: #FF9600; }
    .slider_item-bottom_stars :global(.half) {
      color: #DFE0E1; }
